@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: "Poppins", sans-serif;
}

body {
    margin: 0;
    padding: 0;
}

.task-item {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 1000;
}

.modal-content {
  background-color: white;
  padding: 2em;
  border-radius: 10px;
  text-align: center;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

.modal-content h2 {
  margin-bottom: 1em;
}

.modal-content input {
  padding: 0.5em;
  font-size: 1em;
  margin-bottom: 1em;
  width: 100%;
}

.modal-content button {
  padding: 0.5em 1em;
  font-size: 1em;
  background-color: #fffafa;
  color: #0f0f0f;
  border: 1px solid black;
  border-radius: 5px;
  cursor: pointer;
}

.modal-content button:hover {
  background-color: #212121;
  color: #fffafa;
}

.home {
    display: flex;
}

.navigation {
    position: relative;
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;
    align-items: center;
    justify-content: flex-start;
    gap: 4rem;
    width: 20rem;
    height: 100vh;
    border-right: 1px solid black;
    overflow: hidden;
}

.navigation h3 {
    color: #858787;
    font-weight: 400;
    font-size: 20px;
}

.navigation h2 {
    font-size: 18px;
}

.nav-title {
    display: flex;
    margin: 2rem 0 0 0;
    width: 80%;
}

.nav-title img {
    width: 60px; 
    height: 60px; 
    border-radius: 50%; 
    margin-right: 0.5em;
}

.nav-menu {
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;
    text-align: left;
    width: 80%;
    gap: 20px;
}

.nav-recent {
    display: flex;
    flex-direction: column;
    width: 80%;
}

.nav-lists {
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 20px;
    max-height: 700px;
    overflow: hidden;
    overflow-y: auto;
    padding: 0 2rem;
}

.nav-recent h2, .nav-lists h2 {
    font-weight: 400;
}

.navigation-img {
    background-size: cover; 
    width: 100%; 
    height: 200px;
    position: absolute;
    bottom: 0;
    margin-bottom: 6rem;
}

.main {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    text-align: left;
    margin: 2rem;
    padding: 8rem;
    width: 1100px;
    min-width: 900px;
    border: 2px solid black;
}

.actions {
    display: flex;
    gap: 10px;
    justify-content: flex-end;
    width: 100%;
}

.main-title {
    width: 100%;
    text-align: left;
    font-size: 28px;
}

.main-description {
    color: #858787;
    font-weight: 600;
    margin-bottom: 2rem;
    text-align: left;
    width: 100%;
}

.main-date {
    display: flex;
    gap: 20px;
    margin: 1rem;
    justify-content: flex-start;
    width: 100%;
    color: #858787;
    border-bottom: 5px solid black;
}

.sidebar {
  display: flex;
  flex-direction: column;
  margin: 2rem 0;
  padding: 3rem;
  width: 400px;
  height: 920px;
  border: 2px solid black;
  position: relative;
  transition: border 0.3s;
}

.sidebar h3, .sidebar h4  {
    font-weight: 600;
    margin-bottom: 1rem;
}

.lists-tasks {
    max-height: 400px;
    overflow: hidden;
    overflow-y: auto;
}

.side-button {
    position: absolute; /* Positioning it absolutely within the sidebar */
    bottom: 30px; /* Align it to the bottom */
    left: 0; /* Align to the left */
    width: 100%; /* Full width of the parent */
    display: flex;
    justify-content: center;
    gap: 20px;
    background-color: white; /* Optional: background color to ensure it's visible */
    padding: 10px; /* Optional: padding for visual spacing */
}

.sidebar .input-container {
  position: relative;
  margin: 20px auto; /* Adjust as needed for spacing */
  width: 100%; /* Width of input containers */
}

.sidebar .input-container input[type="text"] {
  font-size: 16px; /* Font size */
  width: 100%;
  border: none; /* Remove default border */
  border-bottom: 2px solid #ccc; /* Bottom border */
  padding: 5px 0; /* Padding for input */
  background-color: transparent; /* Transparent background */
  outline: none; /* Remove outline */
}

.input-container .label {
  position: absolute;
  top: 0;
  left: 0;
  color: #ccc; /* Default label color */
  transition: all 0.3s ease; /* Smooth transition for label */
  pointer-events: none; /* Prevent pointer events on label */
}

.input-container input[type="text"]:focus ~ .label,
.input-container input[type="text"]:valid ~ .label {
  top: -20px; /* Move label above input when focused or filled */
  font-size: 16px; /* Smaller font size */
  color: #333; /* Change label color */
}

.input-container .underline {
  position: absolute;
  bottom: 0; /* Position underline at bottom */
  left: 0; /* Align left */
  height: 2px; /* Height of underline */
  width: 100%; /* Full width */
  background-color: #333; /* Underline color */
  transform: scaleX(0); /* Initially hidden */
  transition: all 0.3s ease; /* Smooth transition */
}

.input-container input[type="text"]:focus ~ .underline,
.input-container input[type="text"]:valid ~ .underline {
  transform: scaleX(1); /* Show underline on focus or valid input */
}

.create-folder-button {
    width: 200px;
  cursor: pointer;
  font-weight: 700;
  transition: all 0.2s;
  padding: 10px 20px;
  border-radius: 100px;
  background: #fffafa;
  display: flex;
  align-items: center;
  font-size: 15px;
  border: 1px solid black;
}

.create-folder-button:hover {
  background: #e8e8e8;
}

.create-folder-button > svg {
  width: 34px;
  margin-left: 10px;
  transition: transform 0.3s ease-in-out;
}

.create-folder-button:hover svg {
  transform: translateX(5px);
}

.create-folder-button:active {
  transform: scale(0.95);
}

.add-task-button {
    width: 200px;
  font-family: inherit;
  font-size: 20px;
  background: #212121;
  color: white;
  fill: rgb(155, 153, 153);
  padding: 0.7em 1em;
  padding-left: 0.9em;
  display: flex;
  align-items: center;
  cursor: pointer;
  border: none;
  border-radius: 100px;
  font-weight: 1000;
}

.add-task-button span {
  display: block;
  margin-left: 0.3em;
  transition: all 0.3s ease-in-out;
}

.add-task-button svg {
  display: block;
  transform-origin: center center;
  transition: transform 0.3s ease-in-out;
}

.add-task-button:hover {
  background: #000;
}

.add-task-button:hover .svg-wrapper {
  transform: scale(1.25);
  transition: 0.5s linear;
}

.add-task-button:hover svg {
  transform: translateX(1.2em) scale(1.1);
  fill: #fff;
}

.add-task-button:hover span {
  opacity: 0;
  transition: 0.5s linear;
}

.add-task-button:active {
  transform: scale(0.95);
}

.ui-bookmark {
  --icon-size: 24px;
  --icon-secondary-color: rgb(164, 164, 164);
  --icon-hover-color: rgb(165, 164, 164);
  --icon-primary-color: gold;
  --icon-circle-border: 1px solid var(--icon-primary-color);
  --icon-circle-size: 35px;
  --icon-anmt-duration: 0.3s;
}

.ui-bookmark input {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  display: none;
}

.ui-bookmark .bookmark {
  width: var(--icon-size);
  height: auto;
  fill: var(--icon-secondary-color);
  cursor: pointer;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  position: relative;
  -webkit-transform-origin: top;
  -ms-transform-origin: top;
  transform-origin: top;
}

.bookmark::after {
  content: "";
  position: absolute;
  width: 10px;
  height: 10px;
  -webkit-box-shadow: 0 30px 0 -4px var(--icon-primary-color),
    30px 0 0 -4px var(--icon-primary-color),
    0 -30px 0 -4px var(--icon-primary-color),
    -30px 0 0 -4px var(--icon-primary-color),
    -22px 22px 0 -4px var(--icon-primary-color),
    -22px -22px 0 -4px var(--icon-primary-color),
    22px -22px 0 -4px var(--icon-primary-color),
    22px 22px 0 -4px var(--icon-primary-color);
  box-shadow: 0 30px 0 -4px var(--icon-primary-color),
    30px 0 0 -4px var(--icon-primary-color),
    0 -30px 0 -4px var(--icon-primary-color),
    -30px 0 0 -4px var(--icon-primary-color),
    -22px 22px 0 -4px var(--icon-primary-color),
    -22px -22px 0 -4px var(--icon-primary-color),
    22px -22px 0 -4px var(--icon-primary-color),
    22px 22px 0 -4px var(--icon-primary-color);
  border-radius: 50%;
  -webkit-transform: scale(0);
  -ms-transform: scale(0);
  transform: scale(0);
}

.bookmark::before {
  content: "";
  position: absolute;
  border-radius: 50%;
  border: var(--icon-circle-border);
  opacity: 0;
}

.ui-bookmark:hover .bookmark {
  fill: var(--icon-hover-color);
}

.ui-bookmark input:checked + .bookmark::after {
  -webkit-animation: circles var(--icon-anmt-duration)
    cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards;
  animation: circles var(--icon-anmt-duration)
    cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards;
  -webkit-animation-delay: var(--icon-anmt-duration);
  animation-delay: var(--icon-anmt-duration);
}

.ui-bookmark input:checked + .bookmark {
  fill: var(--icon-primary-color);
  -webkit-animation: bookmark var(--icon-anmt-duration) forwards;
  animation: bookmark var(--icon-anmt-duration) forwards;
  -webkit-transition-delay: 0.3s;
  -o-transition-delay: 0.3s;
  transition-delay: 0.3s;
}

.ui-bookmark input:checked + .bookmark::before {
  -webkit-animation: circle var(--icon-anmt-duration)
    cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards;
  animation: circle var(--icon-anmt-duration)
    cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards;
  -webkit-animation-delay: var(--icon-anmt-duration);
  animation-delay: var(--icon-anmt-duration);
}

@-webkit-keyframes bookmark {
  50% {
    -webkit-transform: scaleY(0.6);
    transform: scaleY(0.6);
  }

  100% {
    -webkit-transform: scaleY(1);
    transform: scaleY(1);
  }
}

@keyframes bookmark {
  50% {
    -webkit-transform: scaleY(0.6);
    transform: scaleY(0.6);
  }

  100% {
    -webkit-transform: scaleY(1);
    transform: scaleY(1);
  }
}

@-webkit-keyframes circle {
  from {
    width: 0;
    height: 0;
    opacity: 0;
  }

  90% {
    width: var(--icon-circle-size);
    height: var(--icon-circle-size);
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

@keyframes circle {
  from {
    width: 0;
    height: 0;
    opacity: 0;
  }

  90% {
    width: var(--icon-circle-size);
    height: var(--icon-circle-size);
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

@-webkit-keyframes circles {
  from {
    -webkit-transform: scale(0);
    transform: scale(0);
  }

  40% {
    opacity: 1;
  }

  to {
    -webkit-transform: scale(0.8);
    transform: scale(0.8);
    opacity: 0;
  }
}

@keyframes circles {
  from {
    -webkit-transform: scale(0);
    transform: scale(0);
  }

  40% {
    opacity: 1;
  }

  to {
    -webkit-transform: scale(0.8);
    transform: scale(0.8);
    opacity: 0;
  }
}
/* From Uiverse.io by catraco */ 
.heart-container {
  --heart-color: rgb(255, 91, 137);
  position: relative;
  width: 25px;
  height: 25px;
  transition: .3s;
}

.heart-container .checkbox {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 20;
  cursor: pointer;
}

.heart-container .svg-container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.heart-container .svg-outline,
        .heart-container .svg-filled {
  fill: var(--heart-color);
  position: absolute;
}

.heart-container .svg-filled {
  animation: keyframes-svg-filled 1s;
  display: none;
}

.heart-container .svg-celebrate {
  position: absolute;
  animation: keyframes-svg-celebrate .5s;
  animation-fill-mode: forwards;
  display: none;
  stroke: var(--heart-color);
  fill: var(--heart-color);
  stroke-width: 2px;
}

.heart-container .checkbox:checked~.svg-container .svg-filled {
  display: block
}

.heart-container .checkbox:checked~.svg-container .svg-celebrate {
  display: block
}

@keyframes keyframes-svg-filled {
  0% {
    transform: scale(0);
  }

  25% {
    transform: scale(1.2);
  }

  50% {
    transform: scale(1);
    filter: brightness(1.5);
  }
}

@keyframes keyframes-svg-celebrate {
  0% {
    transform: scale(0);
  }

  50% {
    opacity: 1;
    filter: brightness(1.5);
  }

  100% {
    transform: scale(1.4);
    opacity: 0;
    display: none;
  }
}

#checklist {
  /* border: 1px solid black; */
  --background: #fff;
  --text: #414856;
  --check: #4f29f0;
  --disabled: #c3c8de;
  --width: 100%;
  --height: auto;
  --border-radius: 10px;
  background: var(--background);
  width: var(--width);
  height: var(--height);
  border-radius: var(--border-radius);
  position: relative;
  /* padding: 30px 85px; */
  display: grid;
  grid-template-columns: 30px auto;
  align-items: center;
  justify-content: left;
  max-height: 600px;
  gap: 10px;
  margin-top: 3rem;
}

#checklist label {
  color: var(--text);
  position: relative;
  cursor: pointer;
  display: grid;
  align-items: center;
  width: fit-content;
  transition: color 0.3s ease;
  margin-right: 20px;
}

#checklist label::before, #checklist label::after {
  content: "";
  position: absolute;
}

#checklist label::before {
  height: 2px;
  width: 8px;
  left: -27px;
  background: var(--check);
  border-radius: 2px;
  transition: background 0.3s ease;
}

#checklist label:after {
  height: 4px;
  width: 4px;
  top: 8px;
  left: -25px;
  border-radius: 50%;
}

#checklist input[type="checkbox"] {
  -webkit-appearance: none;
  -moz-appearance: none;
  position: relative;
  height: 15px;
  width: 15px;
  outline: none;
  border: 0;
  margin: 0 15px 0 0;
  cursor: pointer;
  background: var(--background);
  display: grid;
  align-items: center;
  margin-right: 20px;
}

#checklist input[type="checkbox"]::before, #checklist input[type="checkbox"]::after {
  content: "";
  position: absolute;
  height: 2px;
  top: auto;
  background: var(--check);
  border-radius: 2px;
}

#checklist input[type="checkbox"]::before {
  width: 0px;
  right: 60%;
  transform-origin: right bottom;
}

#checklist input[type="checkbox"]::after {
  width: 0px;
  left: 40%;
  transform-origin: left bottom;
}

#checklist input[type="checkbox"]:checked::before {
  animation: check-01 0.4s ease forwards;
}

#checklist input[type="checkbox"]:checked::after {
  animation: check-02 0.4s ease forwards;
}

#checklist input[type="checkbox"]:checked + label {
  color: var(--disabled);
  animation: move 0.3s ease 0.1s forwards;
}

#checklist input[type="checkbox"]:checked + label::before {
  background: var(--disabled);
  animation: slice 0.4s ease forwards;
}

#checklist input[type="checkbox"]:checked + label::after {
  animation: firework 0.5s ease forwards 0.1s;
}

@keyframes move {
  50% {
    padding-left: 8px;
    padding-right: 0px;
  }

  100% {
    padding-right: 4px;
  }
}

@keyframes slice {
  60% {
    width: 100%;
    left: 4px;
  }

  100% {
    width: 100%;
    left: -2px;
    padding-left: 0;
  }
}

@keyframes check-01 {
  0% {
    width: 4px;
    top: auto;
    transform: rotate(0);
  }

  50% {
    width: 0px;
    top: auto;
    transform: rotate(0);
  }

  51% {
    width: 0px;
    top: 8px;
    transform: rotate(45deg);
  }

  100% {
    width: 5px;
    top: 8px;
    transform: rotate(45deg);
  }
}

@keyframes check-02 {
  0% {
    width: 4px;
    top: auto;
    transform: rotate(0);
  }

  50% {
    width: 0px;
    top: auto;
    transform: rotate(0);
  }

  51% {
    width: 0px;
    top: 8px;
    transform: rotate(-45deg);
  }

  100% {
    width: 10px;
    top: 8px;
    transform: rotate(-45deg);
  }
}

@keyframes firework {
  0% {
    opacity: 1;
    box-shadow: 0 0 0 -2px #4f29f0, 0 0 0 -2px #4f29f0, 0 0 0 -2px #4f29f0, 0 0 0 -2px #4f29f0, 0 0 0 -2px #4f29f0, 0 0 0 -2px #4f29f0;
  }

  30% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    box-shadow: 0 -15px 0 0px #4f29f0, 14px -8px 0 0px #4f29f0, 14px 8px 0 0px #4f29f0, 0 15px 0 0px #4f29f0, -14px 8px 0 0px #4f29f0, -14px -8px 0 0px #4f29f0;
  }
}

