/* From Uiverse.io by mrhyddenn */ 
.edit-button {
  background: none;
  border: none;
  padding: 15px 15px;
  border-radius: 10px;
  cursor: pointer;
  background-color: #212121;
  width: 100px;
  color: #fffafa;
  font-size: 15px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.edit-button:hover {
  background: #fffafa;
  color: #212121;
  transition: 0.5s;
  border: 1px solid #212121;
  height: 30px;
}

.edit-button svg {
  color: #fff;
}

